<template>
    <div class="p-wujie-cj-detail">
        <div class="m-header">
            <div class="m-header__logo">
                <a class="m-logo" :href="rootPath">
                    <img class="u-logo" src="@/assets/img/logo.png" alt="JX3BOX" />
                </a>
