                            <span>:</span>
                            <b>{{ count[key] }}</b>
                        </span>
                    </div>
                </div>
            </div>
            <div class="m-header__links">
                <a class="u-link" :href="`${rootPath}adventure`">奇遇大全</a>
                <a class="u-link" :href="`${rootPath}pet`">宠物大全</a>
                <a class="u-link" :href="`${rootPath}horse`">坐骑大全</a>
                <a class="u-link" :href="`${rootPath}furniture`">家具大全</a>
            </div>
        </div>
        <div class="m-content-wrapper">
            <div class="m-strategy-content">
                <!-- 进阶类成就 -->
                <div class="m-advance"></div>
                <!-- 攻略 -->
                <div class="m-strategy">
                    <div class="m-title">
                        <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18" fill="none" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M14.718 9.842l1.195-1.176-.568-1.577.568-1.578-1.195-1.176-.176-1.667-1.586-.54L12.076.7l-1.664.201L9 0 7.588.901l-1.665-.2-.88 1.427-1.585.54-.176 1.667-1.195 1.176.568 1.578-.568 1.577 1.195 1.176.176 1.667.88.3-1.509 4.718 2.584-.143L7.434 18l1.281-4.004.285.181.285-.181L10.565 18l2.022-1.616 2.584.143-1.51-4.718.881-.3.176-1.667zm-7.822 6.376l-1.139-.91-1.456.08.957-2.99.665 1.079 1.665-.201.207.132-.9 2.81zm5.347-.91l-1.139.91-.899-2.81.207-.132 1.665.201.665-1.079.956 2.99-1.455-.08zm1.32-4.58l-1.306.445-.724 1.175-1.37-.165L9 12.925l-1.163-.742-1.37.165-.724-1.175-1.306-.445-.145-1.373-.983-.967.467-1.3L3.31 5.79l.983-.968.145-1.372 1.306-.446.724-1.175 1.37.166L9 1.252l1.163.743 1.37-.166.724 1.175 1.306.446.145 1.372.983.968-.468 1.299.468 1.299-.983.967-.145 1.373z" fill="#FFF"/><path d="M9 2.98A4.112 4.112 0 004.892 7.09 4.112 4.112 0 009 11.196a4.112 4.112 0 004.108-4.107A4.112 4.112 0 009 2.98zm0 7.16A3.056 3.056 0 015.948 7.09 3.056 3.056 0 019 4.037a3.056 3.056 0 013.052 3.052A3.056 3.056 0 019 10.14z" fill="#FFF"/></svg>
                        <div class="u-title">成就攻略</div>
                    </div>
                    <div class="m-detail">
                        <div class="m-wiki-post-panel" v-if="wiki_post && wiki_post.post">
                            <WikiPanel :wiki-post="wiki_post">
                                <div slot="meta" class="m-meta-list">
                                    <!-- 参与贡献 -->
                                    <div class="m-user-info">
                                        <div class="u-meta" v-if="wiki_post.users && wiki_post.users.length">
                                            <em class="u-label">参与贡献</em>
                                            <div class="m-users">
                                                <a
                                                    class="u-value u-creator"
                                                    v-for="(user, key) in wiki_post.users"
                                                    :key="key"
                                                    target="_blank"
                                                    :href="user.id ? author_url(user.id) : null"
                                                >
                                                    <img
                                                        class="u-user"
                                                        :src="thumbnail_url(user.avatar)"
                                                        :alt="user.nickname"
                                                        :title="user.nickname"
                                                    />
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="m-article-info">
                                        <div class="m-version" v-if="versions.length">
                                            攻略版本：v{{ versions.length }}
                                        </div>
                                        <div class="m-wiki-signature">
                                            本次修订由 <b>{{ user_name }}</b> 提交于{{ updated_at }}
                                        </div>
                                    </div>
                                </div>
                                <template slot="body">
                                    <div class="m-wiki-compatible" v-if="compatible">
                                        <i class="el-icon-warning-outline"></i>
                                        暂无缘起攻略，以下为重制攻略，仅作参考，<a
                                            class="s-link"
                                            :href="publish_url(`achievement/${id}`)"
                                            >参与修订</a
                                        >。
                                    </div>
                                    <Article :content="wiki_post.post.content" />
                                </template>
                            </WikiPanel>
                        </div>

                        <div class="m-wiki-post-empty" v-if="is_empty">
                            <i class="el-icon-s-opportunity"></i>
                            <span>暂无攻略，我要</span>
                            <a class="s-link" :href="publish_url(`achievement/${id}`)">完善攻略</a>
                        </div>
                    </div>
                </div>
            </div>
            <!-- 评论 -->
            <div class="m-comment">
                <div class="m-title">
                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18" fill="none" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" fill="#FFF" d="M2.25 2.25A.75.75 0 001.5 3v13.875l3.341-2.625H15.75a.75.75 0 00.75-.75V3a.75.75 0 00-.75-.75H2.25zM3 3.75v10.039l1.322-1.039H15v-9H3zm2.625 4.879c0 .746.208 1.207.594 1.616a1.45 1.45 0 001.03.443c.725 0 1.313-.588 1.313-1.313a1.32 1.32 0 00-1.21-1.309 1.645 1.645 0 00-.36.007v-.012c0-.256-.002-1.32 1.23-1.986l-.336-.516c-1.34.581-2.261 1.757-2.261 3.07zm3.75 0c0 .746.208 1.207.594 1.616a1.45 1.45 0 001.03.443c.725 0 1.313-.588 1.313-1.313a1.32 1.32 0 00-1.21-1.309 1.645 1.645 0 00-.36.007v-.012c0-.257-.002-1.32 1.23-1.986l-.336-.516c-1.34.581-2.261 1.757-2.261 3.07z"/></svg>
                    <div class="u-title">百科评论</div>
                </div>
                <WikiComments type="achievement" :source-id="id" />
            </div>
        </div>
    </div>
</template>

<script>
import { __Root, __OriginRoot } from "@jx3box/jx3box-common/data/jx3box.json";

import { postStat } from "@jx3box/jx3box-common/js/stat";
import { wiki } from "@jx3box/jx3box-common/js/wiki_v2";
import { publishLink } from "@jx3box/jx3box-common/js/utils";
import { ts2str, authorLink, showAvatar } from "@jx3box/jx3box-common/js/utils.js";
import { reportNow } from "@jx3box/jx3box-common/js/reporter";

import { getAchievementsTotal, get_achievement } from "@/service/achievement";

import WikiPanel from "@/components/wiki-panel.vue";
import Article from "@jx3box/jx3box-editor/src/Article.vue";
import WikiComments from "@/components/wiki-comments.vue";
export default {
    name: "WujieDetail",
    components: {
        WikiPanel,
        Article,
        WikiComments,
    },
    data() {
        return {
            params: new URLSearchParams(location.search),
            generalNum: 137, // 和游戏差异，固定加上
            count: {},
            countDict: {
                general: "常规成就数",
                general_point: "常规资历数",
                armor: "五甲成就数",
                armor_point: "五甲资历数",
                // post_count: "收录攻略数",
            },

            source: "",
            wiki_post: "",
            compatible: false,
            is_empty: true,
            versions: [],
        };
    },
    computed: {
        client: function () {
            let client = this.params.get("L") == "classic_yq" ? "origin" : "std";
            return client;
        },
        rootPath: function () {
            return this.client == "origin" ? __OriginRoot : __Root;
        },
        id() {
            return this.$route.params.source_id;
        },
        post_id: function () {
            return this.$route.params.post_id;
        },
        isRevision: function () {
            return !!this.$route.params.post_id;
        },
        author_id: function () {
            return ~~this.wiki_post.post.user_id;
        },
        user_name: function () {
            return this.wiki_post?.post?.user_nickname;
        },
        updated_at: function () {
            return ts2str(this.wiki_post?.post?.updated);
        },
        authors: function () {
            if (!this.isRevision) {
                return (
                    this.wiki_post?.users
                        ?.filter((user) => user.id)
                        ?.map((user) => {
                            return {
                                user_id: user.id,
                                user_avatar: user.avatar,
                                display_name: user.nickname,
                            };
                        }) || []
                );
            }
            return [];
        },
        favTitle: function () {
            return this.wiki_post?.source?.Name;
        },
        prefix() {
            return this.client === "std" ? "www" : "origin";
        },
    },
    watch: {
        id: {
            handler() {
                this.loadData();
            },
        },
        post_id: {
            handler() {
                this.loadRevision();
            },
        },
    },
    created() {
        this.getStat();
    },
    mounted() {
        if (this.post_id) {
            this.loadRevision();
        } else {
            this.loadData();
        }
    },
    methods: {
        author_url: authorLink,
        ts2str,
        thumbnail_url: function (val) {
            return showAvatar(val);
        },
        publish_url: publishLink,
        triggerStat: function () {
            if (this.client == "origin") {
                postStat("origin_cj", this.id);
            } else {
                postStat("cj", this.id);
            }
        },
        loadData: function () {
            // 获取最新攻略
            if (this.id) {
                get_achievement(this.id, { client: this.client }).then((res) => {
                    this.source = res?.data?.data?.achievement || "";
                });
                wiki.mix({ type: "achievement", id: this.id, client: this.client }).then((res) => {
                    const { post, source, compatible, isEmpty, users } = res;
                    this.wiki_post = {
                        post: post,
                        source: source,
                        users,
                    };
                    this.is_empty = isEmpty;
                    this.compatible = compatible;

                    reportNow({
                        caller: "cj_detail",
                        data: {
                            href: `${this.prefix}:/cj/view/${this.id}`,
                        },
                    });
                });

                wiki.versions({ type: "achievement", id: this.id }, { client: this.client }).then(
                    (res) => {
                        res = res.data;
                        this.versions = res.data || [];
                    },
                    () => {
                        this.versions = [];
                    }
                );
            }
            this.triggerStat();
        },
        loadRevision: function () {
            // 获取指定攻略
            wiki.getById(this.post_id).then((res) => {
                this.wiki_post = {
                    ...this.wiki_post,
                    post: res.data.data?.post,
                };
            });
            this.triggerStat();
        },
        getStat() {
            // 获取成就统计信息
            getAchievementsTotal().then((data) => {
                data = data.data;
                this.count = data.data.count;
                this.count.general = ~~this.count?.general + this.generalNum;
            });
        },
    },
};
</script>

<style lang="less">
@import "~@/assets/css/wujie/cj-detail.less";
